.date-widget__content {
  margin: 0 auto;
  padding-bottom: 25px;
  width: 180px;
  display: flex;
  align-items: center;
  justify-content: space-between;

  & p {
    font-size: 28px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: -1.4px;
  }
}

.date-widget__item {
  padding: 5px 10px;
  color: #1e2022;
  font-family: 'Roboto Mono';
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: -1px;
  border-radius: 3px;
  background: #fff;
  box-shadow: 0px 1px 4px 4px rgba(82, 97, 106, 0.25);
}
